/* Quote Form Styles - Scoped to prevent affecting other pages */
.quoteFormContainer {
  background: #f5f5f5;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px 20px;
}

.formTitle {
  font-size: 28px;
  font-weight: 600;
  color: #000;
  margin-bottom: 30px;
  text-align: center;
}

.quoteForm {
  background: #ffffff;
  padding: 40px;
  border-radius: 4px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 600px;
}

.formField {
  margin-bottom: 24px;
}

.formLabel {
  display: block;
  font-size: 14px;
  font-weight: 500;
  color: #333;
  margin-bottom: 8px;
}

.formInput,
.formTextarea {
  width: 100%;
  padding: 10px 12px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 14px;
  font-family: inherit;
  transition: border-color 0.2s;
  box-sizing: border-box;
}

.formInput:focus,
.formTextarea:focus {
  outline: none;
  border-color: #4a90e2;
}

.formTextarea {
  resize: vertical;
  min-height: 100px;
}

.fileUploadContainer {
  display: flex;
  align-items: center;
  gap: 12px;
}

.fileButton {
  padding: 8px 16px;
  background: #f0f0f0;
  border: 1px solid #ccc;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  color: #333;
  transition: background-color 0.2s;
}

.fileButton:hover {
  background: #e0e0e0;
}

.fileInput {
  display: none;
}

.fileStatus {
  font-size: 14px;
  color: #666;
}

.submitContainer {
  margin-top: 32px;
  display: flex;
  justify-content: center;
}

.submitButton {
  padding: 12px 32px;
  background: #4a90e2;
  color: #ffffff;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s;
}

.submitButton:hover:not(:disabled) {
  background: #357abd;
}

.submitButton:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

